@import url(https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,300;0,700;1,200&display=swap);
*{font-family:"Nunito", sans-serif}.shop{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:center;align-items:center;margin:auto}.till{max-width:75%;margin:auto}.card,.product,.message__container{padding:2rem;display:flex;flex-direction:column;align-items:center;justify-content:center;border-radius:5px;border:1px solid #ddd}.card h2,.product h2,.message__container h2{color:#333}.btn_container{display:flex;align-items:center}button{border-radius:5px;border:solid white 2px;color:#333;cursor:pointer}button:disabled{background-color:#ddd}.btn-add-to-cart{padding:0.8rem;background-color:#91f291}.btn-add-to-cart:hover{border:solid #61f2c2 2px}.btn-add-to-cart:disabled{background-color:#ddd}.btn-add-to-cart:disabled:hover{border:2px solid white}.btn_container .btn-quantity,.btn_container .btn-quantity--plus,.btn_container .btn-quantity--minus{padding:0.5rem 1rem;color:#333;margin:0.5rem;font-weight:700}.btn_container .btn-quantity:disabled,.btn_container .btn-quantity--plus:disabled,.btn_container .btn-quantity--minus:disabled{background-color:#ddd}.btn_container .btn-quantity:hover,.btn_container .btn-quantity--plus:hover,.btn_container .btn-quantity--minus:hover{border:2px solid #333}.btn_container .btn-quantity--plus{background-color:#30f2f2}.btn_container .btn-quantity--minus{background-color:#61f2c2}.quantity{padding:0.5rem;border-radius:5px}.offer{padding:0.7rem;font-size:0.9rem;min-height:2rem;display:flex;align-items:center;justify-content:space-between}.offer span{width:75%}.availible,.availible--out,.availible--in{padding:1rem;text-align:center;border-radius:5px;width:100%}.availible--out{color:#999}.availible--in{color:#333}.shadow--0{box-shadow:1px 2px 2px #91f291}.shadow--1{box-shadow:1px 2px 2px #f2f230}.shadow--2{box-shadow:1px 2px 2px #61f2c2}.shadow--3{box-shadow:1px 2px 2px #f24b3d}.shadow--4{box-shadow:1px 2px 2px #30f2f2}.product{max-width:275px;margin:1rem}.product h2{text-transform:capitalize;padding:0.5rem;border-radius:5px}.product .price{font-size:1.2}.product .img{border:1px solid #ddd;border-radius:5px;width:100%;object-fit:scale-down}.hide{display:none}.message__container{margin-bottom:1.2rem}.message__container h1{font-size:2rem}.message__container .links__container{display:flex;align-items:center}.message__container .links__container a>svg{margin-left:10px;width:45px;fill:blue}

